.section-content-experience {
  display: flex;
  flex-direction: column;
}

.experience-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;

  h3 {
    width: 100%;
    letter-spacing: 1px;
    margin-top: 20px;
    color: var(--f-primary-1);

    span {
      color: var(--primary-3);
    }
  }

  p {
    font-size: .85em;
    width: 100%;
    margin-top: 10px;
    font-family: var(--sf-mono);
    color: var(--f-primary-2);
  }
}

.section-description {
  color: var(--f-primary-2);
  font-family: var(--sf-pro-regular);
  font-size: 1em;
  line-height: 1.5;
}

.section-description-list {
  list-style-type: none;

  li {
    display: flex;
    align-items: center;

    i {
      margin-right: 10px;
      color: var(--primary-2);
    }
  }
}

.experience-content-container {
  position: relative;
  display: inline-flex;
  flex-shrink: 0;
  transition: 0.5s ease-in-out;
  width: 100%;

  .experience-content {
    width: 100%;
    flex: 0 0 100%;
    position: relative;
    transition: 0.5s ease-in-out;
  }
}

.experience-navigator {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
  width: 100%;
  box-sizing: content-box;
  margin-bottom: 20px;

  &::-webkit-scrollbar {
    height: 4px;
  }

  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.6);
    border-radius: 5px;
  }

  &::-webkit-scrollbar-thumb {
    background: var(--f-primary-4);
    border-radius: 10px;
  }

  .experience-tab {
    cursor: pointer;
    border: none;
    font-family: var(--sf-mono);
    flex: 1;
    padding: 16px 10px;
    background-color: transparent;
    color: var(--f-primary-3);
    position: relative;
    outline: none;
    transition: .15s ease-in-out;
    white-space: nowrap;

    &:hover {
      background-color: var(--bg-primary-4);
      color: var(--primary-3);

      &::after {
        background-color: var(--primary-3);
      }
    }

    &::after {
      position: absolute;
      bottom: -2px;
      left: 0;
      width: 100%;
      display: block;
      content: "";
      height: 2px;
      background-color: var(--f-primary-3);
      transition: 0.15s ease-in-out;
    }

    &.active {
      background-color: var(--bg-primary-4);
      color: var(--primary-3);

      &::after {
        background-color: var(--primary-3);
      }
    }
  }
}

@media screen and (min-width: 768px) {
  .section-content-experience {
    flex-direction: row;
  }

  .experience-content-container {
    .experience-content {
      padding: 0 30px;
    }
  }

  .experience-navigator {
    overflow: hidden;
    flex: 0 0 140px;
    box-sizing: content-box;
    flex-direction: column;
    justify-content: flex-start;
    flex-flow: column;

    .experience-tab {
      margin-top: 2px;
      flex: none;
      white-space: normal;

      &::after {
        top: 0;
        left: -2px;
        height: 100%;
        width: 3px;
      }
    }
  }
}
